@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --instagram-top: hsl(329, 70%, 58%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-bottom: hsl(37, 97%, 70%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram: linear-gradient(225deg, var(--instagram-top) 0%, var(--instagram-middle) 50.91%, var(--instagram-bottom) 100%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-top: hsl(210, 78%, 56%);
  --toggle-bottom: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --light-bg: hsl(0, 0%, 100%);
  --light-blue-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-card-text: hsl(228, 12%, 44%);
  --light-text: hsl(230, 17%, 14%);
  --card-fb: .25rem solid var(--facebook);
  --card-tw: .25rem solid var(--twitter);
  --card-yt: .25rem solid var(--youtube);
  --toggle-bg: linear-gradient(225deg, var(--toggle-bottom) 0%, var(--toggle-top) 98.02%);
  --page-bg: var(--light-bg);
  --header-bg: var(--light-blue-top-bg);
  --heading-color: var(--dark-blue-bg);
  --card-bg: var(--light-card-bg);
  --card-text-color: var(--light-card-text);
  --card-num-color: var(--light-text);
}

[data-theme=dark] {
  --dark-blue-bg: hsl(230, 17%, 14%);
  --dark-blue-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-text: hsl(228, 34%, 66%);
  --dark-text: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0 1.5625rem 0 1.5rem;
  padding: 0;
  font-family: var(--font);
  background-color: var(--page-bg);
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  transition: background-color 0.3s ease, color 0.2s ease;
}
@media (min-width: 1440px) {
  body {
    gap: 2.875rem;
  }
}

h1, h2, h3 {
  margin: 0;
}

p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

main {
  display: flex;
  flex-direction: column;
  gap: 2.875rem;
}

:root {
  --font: "Inter", sans-serif;
  --fsize: .875rem;
  --lg-fsize: 3.5rem;
  --md-fsize: 2rem;
  --md-mfsize: 1.5rem;
  --md-dfsize: 1.75rem;
  --xs-fsize: .75rem;
  --lg-lheight: 3rem;
  --md-lheight: 2.4375rem;
  --md-dlheight: 2.125rem;
  --md-mlheight: 1.8125rem;
  --sm-lheight: 1.0625rem;
  --xs-lheight: .9375rem;
  --bold: 700;
  --regular: 400;
}

/* Dashboard numbers varied size -------------------*/
/* Card metrics (followers, subscribers, etc.) and userNames */
/* h1, h2 and paragraph styles for desktop and mobile -----------------*/
h1,
h2 {
  font-weight: var(--bold);
  font-size: var(--md-mfsize);
  line-height: var(--md-mlheight);
}
@media (min-width: 1440px) {
  h1,
  h2 {
    font-weight: var(--bold);
    font-size: var(--md-dfsize);
    line-height: var(--md-dlheight);
  }
}

h2 {
  color: var(--card-text-color);
}

p {
  font-weight: var(--bold);
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
}

.facebook {
  border-top: var(--card-fb);
}

.twitter {
  border-top: var(--card-tw);
}

.instagram::before {
  content: "";
  position: absolute;
  height: 0.25rem;
  width: 100%;
  inset: 0;
  background: var(--instagram);
}

.youtube {
  border-top: var(--card-yt);
}

.positive {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--limeGreen);
}

.negative {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--brightRed);
}

.mdheader {
  padding-bottom: 1.6875rem;
}

.toggle__input {
  position: relative;
  width: 3rem;
  height: 1.5rem;
  appearance: none;
  background-color: var(--toggle);
  border-radius: 0.75rem;
  outline: none;
  cursor: pointer;
  margin: 0;
}

.toggle__input:hover {
  background: var(--toggle-bg);
}

.toggle__input::before {
  content: "";
  position: absolute;
  top: 0.125rem;
  right: 0.125rem;
  width: 1.25rem;
  height: 1.25rem;
  background-color: var(--card-bg);
  border-radius: 50%;
  transition: transform 0.2s ease-in-out;
}

.toggle__input:checked::before {
  transform: translateX(-1.5rem);
}

.card {
  padding: 2rem 6.1875rem 1.5rem 6.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--card-bg);
  position: relative;
  border-radius: 0.3125rem;
  overflow: hidden;
}
@media (min-width: 1440px) {
  .card {
    padding: 2rem 3.625rem 1.5rem 3.625rem;
    max-height: 13.5rem;
    max-width: 15.9375rem;
  }
}
.card__social {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding-bottom: 1.75rem;
}
.card__social--usrName {
  font-weight: var(--bold);
  font-size: var(--xs-fsize);
  line-height: var(--xs-lheight);
  color: var(--card-text-color);
}
.card .card__num {
  text-align: center;
  padding-bottom: 1.5625rem;
}
.card .card__num--lg {
  display: block;
  font-weight: var(--bold);
  font-size: var(--lg-fsize);
  line-height: var(--lg-lheight);
  color: var(--card-num-color);
  padding-bottom: 0.5625rem;
}
.card .card__num--metric {
  font-weight: var(--regular);
  font-size: var(--xs-fsize);
  line-height: var(--sm-lheight);
  text-transform: uppercase;
  color: var(--card-text-color);
  letter-spacing: 0.3125rem;
}
.card__trend {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.25rem;
}

.card:hover {
  background-color: hsl(228, 33%, 91%);
  cursor: pointer;
}

.mdCard {
  display: flex;
  flex-direction: column;
  gap: 1.4375rem;
  padding: 0 1.875rem 0 1.5rem;
  background-color: var(--card-bg);
  border-radius: 0.3125rem;
}
@media (min-width: 1440px) {
  .mdCard {
    padding-right: 1.9375rem;
    max-width: 15.9375rem;
    max-height: 7.8125rem;
  }
}
.mdCard__metrics {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-top: 1.5rem;
  color: var(--card-text-color);
}
.mdCard__metrics--md {
  font-weight: var(--bold);
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
  white-space: nowrap;
}
.mdCard__num {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 1.1875rem;
}
.mdCard__num--lg {
  color: var(--card-num-color);
  font-weight: var(--bold);
  font-size: var(--md-fsize);
  line-height: var(--md-lheight);
}
.mdCard__num--stats {
  display: flex;
  align-items: center;
  gap: 0.1875rem;
}

.mdCard:hover {
  background-color: hsl(228, 33%, 91%);
  cursor: pointer;
}

.container {
  max-width: 68.75rem;
}
@media (min-width: 1440px) {
  .container {
    margin: 0 auto;
  }
}

header {
  width: 100%;
}
@media (min-width: 1440px) {
  header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
header .header {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding-bottom: 1.5rem;
  margin-top: 2.25rem;
  border-bottom: 0.0625rem solid hsl(229, 19%, 59%);
}
@media (min-width: 1440px) {
  header .header {
    padding-bottom: 0;
    gap: 0.1875rem;
    border-bottom: none;
  }
}
header .header > h1 {
  color: var(--heading-color);
  padding-bottom: 0.25rem;
}
header .header > p {
  color: var(--card-text-color);
}
header .toggle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
}
@media (min-width: 1440px) {
  header .toggle {
    padding-top: 0;
    margin-top: 2.25rem;
  }
}
header .toggle label {
  font-weight: 700;
  font-size: var(--fsize);
  line-height: var(--sm-lheight);
  color: var(--card-text-color);
}
@media (min-width: 1440px) {
  header .toggle label {
    padding-right: 0.8125rem;
  }
}

header::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  width: 100%;
  height: 14.6875rem;
  background-color: var(--header-bg);
  border-radius: 0 0 1.25rem 1.25rem;
}

.lgCards {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  gap: 1.5rem;
}
@media (min-width: 1440px) {
  .lgCards {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.625rem;
  }
}

.mdCards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
@media (min-width: 1440px) {
  .mdCards {
    grid-template-columns: repeat(4, 1fr);
  }
}